import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { deleteContact, listAllAccounts } from '../../../store/middlewares/account';
import { apiErrorHandler, loading } from '../../../utils/general';
import { createContact, getAllContacts, updateContact } from '../../../store/middlewares/contact';
import { intrabankPay } from '../../../store/middlewares/payment';
import CurrencyInput from '../../../components/CurrencyInput';
import { useDrawer } from '../../../hooks/drawer';

const PaySuccess = React.lazy(() => import('../MoveIntrabankSuccess/MoveIntrabankSuccess'));

const Form = () => {
  const { t } = useTranslation();
  const { open } = useDrawer();
  const dispatch = useAppAsyncDispatch();

  const [paymentData, updatePaymentData] = useState({
    amount: '',
    description: '',
    destination: ''
  });

  const { accounts, selectedAccount } = useAppSelector((store) => store.account, shallowEqual);

  const destination = useMemo(() => accounts.filter((a) => a.id !== selectedAccount?.id && a.type !== 'cardAccount'), [accounts, selectedAccount]);

  const destinationAccountDetails = useMemo(() => accounts.find((a) => a.accountNumber === paymentData?.destination), [accounts, paymentData]);

  const isValid = useMemo(() => paymentData.amount.length !== 0 && paymentData.description.length !== 0 && paymentData.destination.length !== 0, [paymentData]);

  const pay = useCallback((contactId: string, deleteContactOnError = false) => {
    if (selectedAccount) {
      dispatch(intrabankPay({
        amount: paymentData.amount,
        description: paymentData.description,
        accountId: selectedAccount.id,
        contactId
      }))
        .then(() => {
          open(PaySuccess);
        }).catch((e) => {
          if (deleteContactOnError) { dispatch(deleteContact(contactId)); }
          apiErrorHandler(e);
        });
    }
  }, [paymentData]);

  const submit = useCallback(() => {
    if (selectedAccount && isValid) {
      loading(true);
      dispatch(getAllContacts(selectedAccount.id, 1, 0, undefined, 'selfIntrabank', {
        intrabankAccountNumber: paymentData.destination
      })).then(({ data }) => {
        if (data.length !== 0 && data[0].id) {
          if (data[0].name !== destinationAccountDetails?.label) {
            dispatch(updateContact(data[0].id, {
              accountId: selectedAccount.id,
              name: destinationAccountDetails?.label || '',
            }))
              .then((c) => {
                if (c.id) { pay(c.id, true); }
              }).catch(apiErrorHandler);
          } else {
            pay(data[0].id);
          }
        } else {
          // create contact
          dispatch(createContact({
            accountId: selectedAccount.id,
            name: destinationAccountDetails?.label || '',
            intrabank: {
              accountNumber: paymentData.destination
            }
          })).then((c) => {
            if (c.id) { pay(c.id, true); }
          }).catch(apiErrorHandler);
        }
      }).catch(apiErrorHandler)
        .finally(() => {
          loading(false);
        });
    }
  }, [selectedAccount, paymentData, isValid, destinationAccountDetails]);

  useEffect(() => {
    dispatch(listAllAccounts()).catch(apiErrorHandler);
  }, []);

  return (
    <div className="page">
      <MiniBox
        heading={t('move_funds.title')}
        footer={(
          <Button disabled={!isValid} width="100%" variant="primary" onClick={submit}>
            {t('move_funds.ctaText')}
          </Button>
        )}

      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds.source')}
            </FormLabel>
            <Input
              disabled
              readOnly
              variant="primary"
              defaultValue={selectedAccount?.label}
            />
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds.destination')}
            </FormLabel>
            <Select
              variant="primary"
              value={paymentData.destination}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => updatePaymentData({
                ...paymentData,
                destination: e.target.value
              })}
              placeholder="Select destination"
            >
              {destination.map((a) => <option key={`account-${a.id}`} value={a.accountNumber}>{a.label}</option>)}
            </Select>
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds.amount')}
            </FormLabel>
            <CurrencyInput
              value={paymentData.amount}
              onChange={(v) => {
                updatePaymentData({
                  ...paymentData,
                  amount: v || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds.description')}
            </FormLabel>
            <Input
              variant="primary"
              value={paymentData.description}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => updatePaymentData({
                ...paymentData,
                description: e.target.value
              })}
            />
          </FormControl>
        </Box>
      </MiniBox>

    </div>
  );
};

export default Form;
